*, :after, :before {
  box-sizing: border-box;
}

body {
  color: #303538;
  background: #eee;
  margin: 20px 0;
}

article {
  width: 21cm;
  min-height: 29.7cm;
  background: #fff;
  margin: 0 auto;
  padding: 24px;
  overflow: auto;
}

p {
  margin: .3em 0;
}

h1 {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 25px;
}

h4, h5, h6 {
  font-weight: normal;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

strong {
  color: #088178;
  font-weight: 500;
}

a {
  color: #e5bf58;
  border-bottom: 1px solid;
  text-decoration: none;
}

p, li {
  color: #4b5258;
  line-height: 1.5;
}

h3, h4, h5, h6 {
  margin: 0;
}

h3 {
  font-size: 18px;
  font-weight: bolder;
}

.bio {
  position: relative;
}

.bio .icon {
  fill: #088178;
  margin-right: 4px;
}

.bio a {
  color: inherit;
  border-bottom: none;
  text-decoration: none;
}

.bio {
  margin-bottom: 12px;
}

.bio ul {
  flex-wrap: wrap;
  padding-left: 2px;
  display: flex;
}

.bio li {
  width: 33.3333%;
  margin-bottom: 4px;
}

.projects {
  margin-top: 20px;
}

.projects p {
  color: #4b5258;
}

.projects > ol {
  margin-top: 0;
  padding: 0;
  list-style-position: inside;
}

.projects > ol > li {
  margin-bottom: 12px;
}

.projects > ol > li > header {
  justify-content: space-between;
  display: flex;
}

.projects > ol > li h3 {
  display: inline-block;
}

.skills ul {
  color: #4b5258;
}

.skills ul > li {
  align-items: center;
  margin-bottom: 4px;
  display: flex;
}

.skills ul li > span:nth-child(1) {
  width: 6px;
  height: 6px;
  border: 2px solid #99d8ce;
  border-radius: 50%;
  flex-shrink: 0;
  margin: 0 12px 0 6px;
  display: inline-block;
}

.openSource header img {
  position: absolute;
  top: 0;
  right: 0;
}

.openSource > ul > li {
  margin-bottom: 12px;
}

.openSource header, .jobs header {
  justify-content: space-between;
  display: flex;
  position: relative;
}

.openSource h2, .jobs h2 {
  margin-top: 6px;
}

.jobs header {
  align-items: baseline;
}

.jobs header > h4 {
  color: #666;
  margin-left: 1em;
  font-size: 16px;
}

.jobs header > span {
  color: #999;
  margin-left: auto;
  font-size: 12px;
}

.others li:nth-child(1) {
  margin-bottom: 3px;
}

.others li .icon, .education p .icon {
  margin-right: 4px;
}

.education p {
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding-right: 18px;
  display: flex;
}

.education p span {
  white-space: nowrap;
  margin-bottom: 2px;
}

.education p span:not(:last-child) {
  margin-right: 10px;
}

.education p span:nth-child(3) {
  color: #666;
  margin-left: auto;
  font-size: 14px;
}

.education, .skills, .others {
  border: 1px solid #f0f0f0;
  padding: 40px 8px 12px;
  position: relative;
}

.education {
  padding-bottom: 10px;
}

.education h2, .skills h2, .others h2 {
  color: #fff;
  background: #088178;
  border-radius: 2px 2px 2px 0;
  margin: 0;
  padding: 0 8px;
  font-size: 16px;
  line-height: 24px;
  display: inline-block;
  position: absolute;
  top: 8px;
  left: -9px;
}

.ribbon-cornor {
  width: 0;
  height: 0;
  border-top: 6px solid #06615a;
  border-left: 8px solid #fff;
  position: absolute;
  top: 100%;
  left: 0;
}

.education, .skills {
  border-bottom: none;
}

.projects h2, .openSource h2, .jobs h2 {
  color: #fff;
  background: #088178;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  margin-left: -8px;
  padding: 0 100px 0 10px;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  display: inline-block;
  position: relative;
}

.projects h2:before, .openSource h2:before, .jobs h2:before {
  content: "";
  width: 18px;
  height: 18px;
  background: #fff;
  display: block;
  position: absolute;
  top: 4px;
  right: -9px;
  transform: rotate(45deg);
}

@media (max-width: 812px) {
  body {
    margin: 0;
  }

  article {
    width: auto;
    height: auto;
  }

  .skills ul li > span:nth-child(1) {
    align-self: flex-start;
    margin-top: 9px;
  }
}

@media (max-width: 660px) {
  .bio li {
    width: 50%;
  }

  .education p span:nth-child(3) {
    margin-left: 0;
  }
}

@media (max-width: 476px) {
  .bio li {
    width: 100%;
  }
}

@media (max-width: 400px) {
  .jobs header h4 {
    margin-left: 12px;
  }
}

@media (max-width: 389px) {
  .jobs header {
    flex-wrap: wrap;
  }

  .jobs header h3 {
    width: 100%;
  }

  .jobs header h4 {
    margin-left: 0;
  }
}

@media print {
  body {
    margin: 0;
  }

  .page2 {
    page-break-before: always;
    margin-top: 30px;
  }

  .bio li {
    width: 33.333%;
  }

  .education p span:nth-child(3) {
    margin-left: auto;
  }

  .ribbon-cornor {
    border-left: 8px solid #0000;
  }
}

