*, :after, :before{
  box-sizing: border-box;
}

body {
  background: #eee;
  margin: 20px 0;
  color: #303538;
}

article {
  width: 21cm;
  min-height: 29.7cm;
  background: white;
  margin: 0 auto;
  overflow: auto;
  padding: 24px;
}

p {
  margin: .3em 0;
}

h1 {
  font-size: 25px;
  margin-top: 0;
  margin-bottom: 16px;
}

h4,
h5,
h6 {
  font-weight: normal;
}

ol,
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

strong {
  font-weight: 500;
  color: #088178;
}

a {
  color: #e5bf58;
  text-decoration: none;
  border-bottom: 1px solid;
}

p,
li {
  color: #4b5258;
  line-height: 1.5;
}

h3,
h4,
h5,
h6 {
  margin: 0;
}

h3 {
  font-size: 18px;
  font-weight: bolder;
}

.bio {
  position: relative;
}

.bio .icon {
  fill: #088178;
}

.bio .icon {
  margin-right: 4px;
}

.bio a {
  color: inherit;
  text-decoration: none;
  border-bottom: none;
}

.bio {
  margin-bottom: 12px;
}

.bio ul {
  display: flex;
  flex-wrap: wrap;
  padding-left: 2px;
}

.bio li {
  width: 33.3333%;
  margin-bottom: 4px;
}

.projects {
  margin-top: 20px;
}

.projects p {
  color: #4b5258;
}

.projects>ol {
  margin-top: 0;
  list-style-position: inside;
  padding: 0;
}

.projects>ol>li {
  margin-bottom: 12px;
}

.projects>ol>li>header {
  display: flex;
  justify-content: space-between;
}

.projects>ol>li h3 {
  display: inline-block;
}

.skills ul {
  color: #4b5258;
}

.skills ul>li {
  margin-bottom: 4px;
  display: flex;
  align-items: center;
  /* white-space: pre-wrap; */
}

.skills ul li > span:nth-child(1) {
  display: inline-block;
  width: 6px;
  height: 6px;
  border: 2px solid #99d8ce;
  border-radius: 50%;
  margin: 0 12px 0 6px;
  flex-shrink: 0;
  /* background: #4b5258; */
}

.openSource header img {
  position: absolute;
  top: 0;
  right: 0;
}

.openSource>ul>li {
  margin-bottom: 12px;
}

.openSource header,
.jobs header {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.openSource h2,
.jobs h2 {
  margin-top: 6px;
}

/* .jobs>ol>li {
  margin: 12px 0;
}

.jobs header {
  display: flex;
  color: #4b5258;
  margin-bottom: 12px;
}

.jobs header>h3 {
  font-size: 18px;
  color: #333;
} */
.jobs header {
  align-items: baseline;
}

.jobs header>h4 {
  margin-left: 1em;
  font-size: 16px;
  color: #666;
}

.jobs header>span {
  font-size: 12px;
  margin-left: auto;
  color: #999;
} 

.others li:nth-child(1) {
  margin-bottom: 3px;
}

.others li .icon,
.education p .icon {
  margin-right: 4px;
}

.education p {
  margin: 0;
}

.education p {
  display: flex;
  padding-right: 18px;
  align-items: center;
  flex-wrap: wrap;
}

.education p span{
  white-space: nowrap;
  margin-bottom: 2px;
}

.education p span:not(:last-child) {
  margin-right: 10px;
}

/* .education p span:nth-child(2) {
  margin-right: auto;
} */

.education p span:nth-child(3) {
  color: #666;
  margin-left: auto;
  font-size: 14px;
}

.education, 
.skills,
.others {
  border: 1px solid #f0f0f0;
  position: relative;
  padding: 40px 8px 12px 8px;
}

.education {
  padding-bottom: 10px;
}

.education h2, 
.skills h2,
.others h2 {
  color: white;
  font-size: 16px;
  line-height: 24px;
  background: #088178;
  border-radius: 2px 2px 2px 0 / 2px 2px 2px 0;
  display: inline-block;
  margin: 0;
  padding: 0 8px;
  position: absolute;
  left: -9px;
  top: 8px;
}

.ribbon-cornor {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 6px solid #06615a;
  border-left: 8px solid #fff;
  top: 100%;
  left: 0;
}    

/* .ribbon-cornor::after {
  position: absolute;
  top: -6px;
  left: -8px;
  width: 100%;
  height: 100%;
  border-top: 6px solid rgba(0,0,0,.25);
  border-left: 8px solid #fff;
  content: "";
  display: block;
} */

.education, 
.skills {
  border-bottom: 0;
}

.projects h2,
.openSource h2,
.jobs h2 {
  position: relative;
  font-size: 16px;
  font-weight: 700;
  color: #fff;
  line-height: 24px;
  padding: 0 100px 0 10px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  margin-left: -8px;
  background: #088178;
  display: inline-block;
}

.projects h2::before,
.openSource h2::before,
.jobs h2::before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background: #fff;
  position: absolute;
  right: -9px;
  top: 4px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}


@media (max-width: 812px) {
  body {
    margin: 0;
  }

  article {
    width: auto;
    height: auto;
  }

  .skills ul li > span:nth-child(1) {
    align-self: flex-start;
    margin-top: 9px;
  }
}

@media (max-width: 660px) {
  .bio li {
    width: 50%;
  }

  .education p span:nth-child(3) {
    margin-left: 0;
  }
}

/* @media (max-width: 576px) {
  .education p {
    display: block;
  }
} */

@media (max-width: 476px) {
  .bio li {
    width: 100%;
  }
}

@media (max-width: 400px) {
  .jobs header h4 {
    margin-left: 12px;
  }
}

@media (max-width: 389px) {
  .jobs header {
    flex-wrap: wrap;
  }
  .jobs header h3 {
     width: 100%;
  }
  .jobs header h4 {
    margin-left: 0;
  }
}



@media print {
  body {
    margin: 0;
  }

  .page2 {
    page-break-before: always;
    margin-top: 30px;
  }

  .bio li {
    width: 33.333%;
  }

  .education p span:nth-child(3) {
    margin-left: auto;
  }

  .ribbon-cornor {
    border-left: 8px solid transparent;
  }  

}